.sceneios-section {

    @media (min-width: 99rem) {
        .bx--grid {
            padding-right: 1rem !important;
            padding-left: 0rem !important;
            margin-right: 0 !important;
            margin-left: 10px !important;
            max-width: 100%;
        }

        .get-this-link-btn {
            padding: 0 23px;
        }

        .cds--content {
            padding: 0 0 0rem 0rem !important;
            color: var(--cds-text-primary, #161616);
            will-change: margin-left;
        }

        .fire-safety-head {
            margin: 0 25px 0 25px !important;
        }

        .fire-safety-small {
            margin: 0 25px 0 25px !important;
        }

        .p-1 {
            padding: 0 10px 0 10px !important;
            margin: 0 10px 0 10px;
        }

        .hearderpanel {
            margin-right: 0px !important;
        }
    }


    @media (min-width: 42rem) {
        .bx--grid {
            padding-right: 2rem;
            padding-left: 2rem;
        }

        .get-this-link-btn {
            padding: auto !important;
        }

        .p-1 {
            padding: 0 7px 0 7px;
        }

        .cds--content {
            padding: 0 0 0 0;
            color: var(--cds-text-primary, #161616);
            will-change: margin-left;
        }

        .admin-custom {
            width: 32px;
            margin-right: 20px;
        }

        .fire-safety-head {
            margin: 0 15px 0 15px;
        }

        .fire-safety-small {
            margin: 0 15px 0 15px;
        }

        .hearderpanel {
            margin-right: -60px;
        }
    }

    .text-right {
        text-align: right;
    }

    .under-line {
        text-decoration: underline;
    }

    .pt-30 {
        padding-top: 30px;
    }

    .cds--btn--primary {
        border-width: 1px;
        border-style: solid;
        border-color: transparent;
        background-color: var(--cds-button-primary, #3B3364);
        color: var(--cds-text-on-color, #ffffff);
        font-weight: 700;
        font-size: 13px;
    }

    .cds--btn--primary:active {
        background-color: var(--cds-button-primary-active, #3B3364);
    }

    .cds--btn--primary:hover {
        background-color: var(--cds-button-primary-hover, #3B3364);
    }

    .d-flex-justify-center {
        display: flex;
        justify-content: center;
    }

    .cds--btn-sm {
        padding: 0 20px 0 20px;
        border-radius: 20px;
    }

    .get-this-link-btn {
        width: -webkit-fill-available;
        ;
    }

    .cds--btn--tertiary {
        border-width: 1px;
        border-style: solid;
        border-color: #E9E6F7;
        background-color: #EDF2F9;
        color: #183153;
        margin: 0 7px 0 7px;
        font-size: 13px;
        font-weight: 600;
    }

    .cds--btn--tertiary:focus,
    .cds--btn--tertiary:hover {
        background-color: #3b3364;
        color: #fff;
        color: var(--cds-text-inverse, #fff);
    }

    .cds--btn--tertiary:focus {
        border-color: #3b3364;
    }

    .getlink {
        text-decoration: none;
    }

    a {
        color: transparent;
    }
}